import React, {useState, useEffect} from "react";

function Home(){
    const [message, setMessage] = useState("Default");
    useEffect(() => {
        fetch("/api/v1/greeting")
        .then(res => res.json())
        .then(data => {
            setMessage(data.content);
        })
        .catch(err => {
            console.log(err);
        })
    }, [])

    return (
        <div>
            <h1>{message}</h1>
        </div>
    )
}

export default Home;